import React from 'react';
import styled from 'styled-components';
const Container = styled.div `
    display: flex;
    flex-direction: column;
    max-height: 250px;
    padding: 20px ;
    overflow-y: auto;
    overflow-x: hidden;
`;
const Row = styled.div `
    display: flex;
    flex-direction: row;
    width: 400px;
    justify-content: start;
    align-items: center;
    margin: 6px 0px;
`;
const Sentiment = styled.div `
    font-size: 18px;
    color: darkgrey;
    width: 60px;
`;
const Comment = styled.div `
    margin-left: 20 px;
    width: 320px;
`;
const SentimentCommentDisplay = ({ comments, setShowSentimentComments }) => {
    const renderComments = () => {
        comments = comments.slice(0, 3);
        return comments.map((comment, idx) => {
            return (React.createElement(Row, { key: idx, onClick: () => setShowSentimentComments(false) },
                React.createElement(Sentiment, null, comment[1].toString().slice(0, 4)),
                React.createElement(Comment, null, comment[0])));
        });
    };
    return (React.createElement(Container, null, renderComments()));
};
export default SentimentCommentDisplay;
